.paginator {
    display: flex;
    justify-content: center;
    list-style-type: none;
    padding: 0;
    margin-top: 0.2em;
    margin-bottom: 1em;

    li:not(:last-child) {
        margin-right: 0.2em;
    }

    .page-arrow {
        a {
            height: 100%;
        }
    }
}
